export default{
	data(){
		return{
			detailsId:'',
			detailsTitle:'',
			detailsObj:''
		}
	},
	methods:{
		getNewsDetails(){
			let self = this;
			let data = {
				id:this.detailsId
			}
			console.log(data)
			this.ask('/app/content/getContent','POST',data,function(res){
				self.detailsObj = res.data.data;
				console.log(res)
			})
		}
	},
	onLoad(option) {
		console.log(option)
		this.detailsId = option.id;
		this.detailsTitle = option.title;
		this.getNewsDetails()
		
	}
}
